import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'tabela-dados',   templateUrl: 'tabela_dados.component.html' }) export class TabelaDadosComponent implements OnInit {       displayMode: TabelaDadosDisplayModeEnum;     displayModeEnum = TabelaDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = TabelaDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = TabelaDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = TabelaDadosDisplayModeEnum.Edit;           break;       }     }  }  enum TabelaDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


